.div-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: beige;
  padding: 20px;
}
.div-nav ul {
  display: flex;
}
.div-nav ul li {
  padding: 30px;
  list-style: none;
  font-family: "Courier New", Courier, monospace;
  font-style: normal;
  font-weight: 600;
  font-size: 25px;
}
.div-li {
  background-color: beige;
  color: white;
  border: 2px solid rgb(207, 207, 148);
  transition: 0.5s;
  border-radius: 20px;
  border: none;
  color: black;
}

.div-li:hover {
  background-color: rgb(207, 207, 148);
  color: white;
  transition: 0.5s;
  color: black;
}
.img-nav{
  width: 100px;
  height: 100px;
  margin-right: 90px
}
